import Vue from 'vue';
import Vuex from 'vuex'
import mutations from '@/store/mutations'
import actions from '@/store/actions'

Vue.use(Vuex);

const store=new Vuex.Store({
        //
        state:{
            topics:[
                {name:'all',desc:''},
                {name:'good',desc:''},
                {name:'share',desc:''},
                {name:'ask',desc:''},
                {name:'job',desc:''},
                {name:'dev',desc:''}
              ],
              unLoginedMoreMenus:{
                login:''
              },
              loginedMoreMenus:{
                center:'',
                createTopic:'',
                message:'',
                logout:''
              },
              isLogined:false,
              loginName:'',
              accesstoken:'',
              isShowMoreMenus:false
        },
        //
        getters:{
            moreMenus:function(state){
                if(state.isLogined==true){
                  return state.loginedMoreMenus;
                }
                
                return state.unLoginedMoreMenus;
                
              },
              getTopicDescByType:function(state){
                return function(tab){
                  for(var index=0;index<state.topics.length;index++){
                    if(state.topics[index].name==tab){
                      return state.topics[index].desc;
                    }
                  }

                };
              }

        },
        mutations,
        actions
});

export default store;


